import React, { Fragment, useEffect, useState } from 'react'
import { emphasize, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import MenuItem from '@material-ui/core/MenuItem';
import clsx from 'clsx';
import Table from '@material-ui/core/Table';
import TableRow from '@material-ui/core/TableRow';
import TableCell from '@material-ui/core/TableCell';
import TableBody from '@material-ui/core/TableBody';
import TableHead from '@material-ui/core/TableHead';
import Switch from '@material-ui/core/Switch';
import Swal from 'sweetalert2'
import TextField from "@material-ui/core/TextField";
import Typography from '@material-ui/core/Typography';
import { getRoles, getMenu, updateMenuRole } from '../../api/api';


const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
        height: 250,
        minWidth: 290
    },
    checkbox: {
        width: 10
    },
    input: {
        display: "flex",
        padding: 0,
        height: "auto"
    },
    valueContainer: {
        display: "flex",
        flexWrap: "wrap",
        flex: 1,
        alignItems: "center",
        overflow: "hidden"
    },
    margin: {
        marginTop: 10
    },
    chip: {
        margin: theme.spacing(0.5, 0.25)
    },
    chipFocused: {
        backgroundColor: emphasize(
            theme.palette.type === "light"
                ? theme.palette.grey[300]
                : theme.palette.grey[700],
            0.08
        )
    },
    noOptionsMessage: {
        padding: theme.spacing(1, 2)
    },
    singleValue: {
        fontSize: 16
    },
    placeholder: {
        position: "absolute",
        left: 2,
        bottom: 6,
        fontSize: 16
    },

    divider: {
        height: theme.spacing(2)
    },
    paper: {
        padding: theme.spacing(2),
        marginTop: 10,
        color: theme.palette.text.secondary,
    },
    button:
    {
        margin: theme.spacing(1),
    },
    fab:
    {
        marginTop: -2,
        float: "right"
    },
    extendedIcon: {
        marginRight: theme.spacing(1),
    },
    formControl: {
        margin: theme.spacing(1),
    },
}));

const WebPermission = () => {

    const [pages, setPages] = useState([]);
    const [roles, setRoles] = useState([]);
    const classes = useStyles();
    const [values, setValues] = useState({
        role: '',
        saved: false
    });

    useEffect(() => {
        getRoles().then(response => {
            setRoles(response.roles);
        });
    }, []);

    const inputChange = prop => event => {
        setValues({ ...values, [prop]: event.target.value });
        listMenu(event.target.value);
    }

    const listMenu = selectedRole => {
        getMenu({ type: "web" }).then(response => {
            const newPages = response.menus.pages.map(page => {
                if (page.roles.includes(selectedRole)) {
                    page.checked = true;
                } else {
                    page.checked = false;
                }
                const newChildrens = page.children.map(children => {
                    if (children.roles.includes(selectedRole)) {
                        children.checked = true;
                    } else {
                        children.checked = false;
                    }
                    return children;
                })
                pages.children = newChildrens;
                return page
            })
            setPages(newPages);
        });
    }

    const onChangeMenu = (index, indexChild) => event => {
        let status = event.target.checked
        let idsToUpdate = [];

        if (pages[index].children.length === 0) {
            const newPages = pages.map((page, idx) => {
                if (idx === index) {
                    page.checked = status;
                    idsToUpdate.push({ page: page._id, status })
                }
                return page
            })
            setPages(newPages);
        } else {
            if (indexChild === -1) {

                const newPages = pages.map((page, idx) => {
                    if (idx === index) {
                        page.checked = status;
                        idsToUpdate.push({ page: page._id, status })
                        const newChildrens = page.children.map(children => {
                            children.checked = status;
                            idsToUpdate.push({ page: children._id, status })
                            return children;
                        })
                        page.children = newChildrens;
                    }
                    return page
                });
                setPages(newPages);
            } else {
                const newPages = pages.map((page, idx) => {
                    if (idx === index) {
                        let countChecked = 0;

                        if (status) {
                            page.checked = status;
                            idsToUpdate.push({ page: page._id, status })
                        }

                        const newChildrens = page.children.map((children, idxChildrend) => {

                            if (idxChildrend === indexChild) {
                                idsToUpdate.push({ page: children._id, status })
                                children.checked = status;
                            } else {
                                if (!status && children.checked) {
                                    countChecked++;
                                }
                            }
                            return children;
                        })
                        if (countChecked === 0 && !status) {
                            page.checked = status;
                            idsToUpdate.push({ page: page._id, status })
                        }
                        page.children = newChildrens;
                    }
                    return page
                });
                setPages(newPages);
            }
        }
        const input = {
            pages: idsToUpdate,
            idrole: values.role,
        }
        updateMenuRole(input).then(_ => { });

    }


    const onSubmitRole = e => {

        const input = {
            name: values.roleName,
            status: true
        }
    }

    const [value, setValue] = React.useState('1');

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };
    return (
        <Fragment>
            <Container>
                <Typography><b>Permisos aplicaci贸n web</b></Typography>
                <Paper className={classes.paper}>
                    <form onSubmit={e => {
                        e.preventDefault()
                        onSubmitRole()
                    }} >
                        <Grid container spacing={1} direction="column" >
                            <Grid item xs={6} >
                                <TextField
                                    fullWidth
                                    select
                                    className={clsx(classes.margin, classes.textField)}
                                    variant="outlined"
                                    label="Rol"
                                    value={values.role}
                                    onChange={inputChange('role')}>
                                    {
                                        roles.map(option => (
                                            <MenuItem key={option._id} value={option._id} >
                                                {option.name}
                                            </MenuItem>))

                                    }
                                </TextField>
                            </Grid>

                            <Grid item xs={6}>

                            </Grid>
                        </Grid>
                    </form>
                </Paper>

                <Grid item xs={12} >
                    <Paper className={classes.paper}>
                        {
                            <Table>

                                <TableBody>
                                    {
                                        pages.map(function (page, index) {

                                            if (page.children.length <= 0) {
                                                return (
                                                    <TableRow key={index}>
                                                        <TableCell>
                                                            <fieldset>
                                                                <legend> <Typography> {page.title} </Typography></legend>
                                                                <Table>
                                                                    <TableHead>
                                                                        <TableRow>
                                                                            <TableCell>{page.title}</TableCell>
                                                                            <TableCell className={classes.checkbox}><Switch checked={page.checked} edge="end" onChange={onChangeMenu(index, -1)} /></TableCell>
                                                                        </TableRow>
                                                                    </TableHead>
                                                                </Table>
                                                            </fieldset>
                                                        </TableCell>
                                                    </TableRow>
                                                )
                                            }
                                            else {
                                                return (
                                                    <TableRow key={index}>
                                                        <TableCell>
                                                            <fieldset>
                                                                <legend> <Typography>{page.title} </Typography></legend>
                                                                <Table>
                                                                    <TableHead>
                                                                        <TableRow>
                                                                            <TableCell>{page.title}</TableCell>
                                                                            <TableCell><Switch edge="end" checked={page.checked} onChange={onChangeMenu(index, -1)} /></TableCell>
                                                                        </TableRow>
                                                                    </TableHead>
                                                                    <TableBody>
                                                                        {
                                                                            page.children.map(function (subPage, indexSubPage) {
                                                                                return (
                                                                                    <TableRow key={indexSubPage}>
                                                                                        <TableCell>{subPage.title} {subPage.checked ? 'checked' : 'none'}</TableCell>
                                                                                        <TableCell className={classes.checkbox} ><Switch edge="end" checked={subPage.checked} onChange={onChangeMenu(index, indexSubPage)} /></TableCell>
                                                                                    </TableRow>
                                                                                )
                                                                            })
                                                                        }
                                                                    </TableBody>
                                                                </Table>
                                                            </fieldset>
                                                        </TableCell>
                                                    </TableRow>


                                                )

                                            }

                                        })
                                    }
                                </TableBody>
                            </Table>
                        }
                    </Paper>
                </Grid>

            </Container>
        </Fragment>
    )
}

export default WebPermission